.form__input {
  padding-right: 10px;
  padding-left: 10px;
  width: 192px;
  height: 36px;
  background-color: #102654;
  border: 1px solid rgba(255, 255, 255, 0);
  border-bottom: 1px solid #ffffff;
  color: #ffffff;
}
.form__input:hover {
  border-bottom: 2px solid #ffffff;
}
.form__input:active, .form__input:focus {
  outline: 2px solid #ffffff;
  border: none;
  color: #ffffff;
}
.form__input:disabled {
  opacity: 0.3;
}

.form__label {
  position: absolute;
  padding-top: 11px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.57;
  color: #ffffff;
  letter-spacing: -1.4px;
}

.form__button {
  margin-top: 28px;
  width: 410px;
  padding-top: 13px;
  padding-bottom: 13px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.57;
  color: #ffffff;
  letter-spacing: 0.7px;
  text-transform: uppercase;
}

.contacts {
  padding-top: 55px;
  padding-right: 159px;
  padding-bottom: 60px;
  padding-left: 123px;
  border-top: 1px solid #74819c;
}

.contacts__title {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.62px;
  color: #102654;
  padding-bottom: 36px;
}

.contacts__address {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 46px;
  row-gap: 14px;
}

.contacts__subtitle {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.5;
  color: #102654;
  font-style: normal;
  padding-bottom: 3px;
}

.contacts__text {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.52;
  color: #102654;
  font-style: normal;
  letter-spacing: -0.2px;
}

